"use client";
import { useState } from 'react';
import { Container, Row, Col, Card, Image } from 'react-bootstrap';

interface Service {
    icon: string;
    title: string;
    description: string;
    color: string;
}

function ServiceSection() {
    const [hoveredIndex, setHoveredIndex] = useState<number | null>(null);

    const services: Service[] = [
        {
            icon: "about-icon1.png",
            title: "Web Development",
            description: "Building responsive and scalable web applications with modern technologies",
            color: "#000000"
        },
        {
            icon: "about-icon2.png",
            title: "UI/UX Design",
            description: "Creating intuitive and beautiful user interfaces that enhance user experience",
            color: "#1a1a1a"
        },
        {
            icon: "about-icon3.png",
            title: "Mobile Development",
            description: "Developing cross-platform mobile applications for iOS and Android",
            color: "#333333"
        }
    ];

    return (
        <section id="services" style={{ padding: '100px 0', background: 'linear-gradient(180deg, #f8f9fa 0%, #ffffff 100%)' }}>
            <Container>
                <Row className="mb-5">
                    <Col md={12} className="text-center">
                        <span 
                            className="badge rounded-pill px-4 py-2 mb-3"
                            style={{
                                background: '#000000',
                                color: 'white',
                                fontSize: '0.9rem',
                                fontWeight: '600'
                            }}
                        >
                            WHAT I OFFER
                        </span>
                        <h2 className="display-4 fw-bold mb-3" style={{ color: '#000000' }}>
                            My Services
                        </h2>
                        <p className="lead text-muted" style={{ maxWidth: '700px', margin: '0 auto' }}>
                            Comprehensive solutions to bring your digital vision to life
                        </p>
                    </Col>
                </Row>

                <Row>
                    {services.map((service, index) => (
                        <Col md={4} key={index} className="mb-4">
                            <Card 
                                className="h-100 border-0 shadow-sm text-center"
                                style={{
                                    borderRadius: '20px',
                                    transform: hoveredIndex === index ? 'translateY(-15px)' : 'translateY(0)',
                                    transition: 'all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275)',
                                    cursor: 'pointer',
                                    overflow: 'hidden',
                                    boxShadow: hoveredIndex === index 
                                        ? '0 20px 40px rgba(0,0,0,0.15)' 
                                        : '0 5px 15px rgba(0,0,0,0.08)'
                                }}
                                onMouseEnter={() => setHoveredIndex(index)}
                                onMouseLeave={() => setHoveredIndex(null)}
                            >
                                <div 
                                    style={{
                                        height: '5px',
                                        background: service.color,
                                        transform: hoveredIndex === index ? 'scaleX(1)' : 'scaleX(0)',
                                        transformOrigin: 'left',
                                        transition: 'transform 0.4s ease'
                                    }}
                                />
                                <Card.Body className="p-5">
                                    <div 
                                        className="mb-4 mx-auto"
                                        style={{
                                            width: '120px',
                                            height: '120px',
                                            borderRadius: '20px',
                                            background: hoveredIndex === index ? service.color : '#f8f9fa',
                                            transition: 'all 0.4s ease',
                                            display: 'flex',
                                            alignItems: 'center',
                                            justifyContent: 'center',
                                            transform: hoveredIndex === index ? 'rotate(10deg) scale(1.1)' : 'rotate(0) scale(1)',
                                            padding: '20px'
                                        }}
                                    >
                                        <Image 
                                            src={`/images/${service.icon}`} 
                                            alt={service.title}
                                            fluid
                                            style={{
                                                filter: hoveredIndex === index ? 'brightness(0) invert(1)' : 'none',
                                                transition: 'filter 0.4s ease',
                                                maxWidth: '80px'
                                            }}
                                        />
                                    </div>
                                    <h4 className="fw-bold mb-3">{service.title}</h4>
                                    <p className="text-muted mb-0" style={{ lineHeight: '1.7' }}>
                                        {service.description}
                                    </p>
                                </Card.Body>
                                <div 
                                    style={{
                                        position: 'absolute',
                                        bottom: 0,
                                        left: 0,
                                        right: 0,
                                        height: '4px',
                                        background: service.color,
                                        transform: hoveredIndex === index ? 'scaleX(1)' : 'scaleX(0)',
                                        transition: 'transform 0.4s ease'
                                    }}
                                />
                            </Card>
                        </Col>
                    ))}
                </Row>

                {/* Additional Services Info */}
                <Row className="mt-5">
                    <Col md={12}>
                        <Card 
                            className="border-0 text-center"
                            style={{
                                background: '#000000',
                                borderRadius: '20px',
                                padding: '50px',
                                color: 'white'
                            }}
                        >
                            <h4 className="fw-bold mb-3">Need Something Else?</h4>
                            <p className="lead mb-4" style={{ opacity: 0.95, maxWidth: '700px', margin: '0 auto' }}>
                                I offer custom solutions tailored to your specific needs. Let's discuss your project!
                            </p>
                            <div>
                                <a 
                                    href="#contact"
                                    className="btn btn-light btn-lg rounded-pill px-5"
                                    onClick={(e) => {
                                        e.preventDefault();
                                        const element = document.getElementById('contact');
                                        element?.scrollIntoView({ behavior: 'smooth' });
                                    }}
                                    style={{
                                        fontWeight: '600',
                                        transition: 'all 0.3s ease'
                                    }}
                                    onMouseEnter={(e) => {
                                        e.currentTarget.style.transform = 'translateY(-3px)';
                                        e.currentTarget.style.boxShadow = '0 10px 30px rgba(255,255,255,0.3)';
                                    }}
                                    onMouseLeave={(e) => {
                                        e.currentTarget.style.transform = 'translateY(0)';
                                        e.currentTarget.style.boxShadow = 'none';
                                    }}
                                >
                                    Get in Touch
                                </a>
                            </div>
                        </Card>
                    </Col>
                </Row>
            </Container>
        </section>
    );
}

export default ServiceSection;