"use client"
import { Container, Row, Col, Card, Image, Button } from 'react-bootstrap';

function AboutSection() {
    const stats = [
        { number: '5+', label: 'Years Experience', icon: 'üìÖ' },
        { number: '50+', label: 'Projects Completed', icon: 'üöÄ' },
        { number: '30+', label: 'Happy Clients', icon: 'üòä' },
        { number: '10+', label: 'Awards Won', icon: 'üèÜ' }
    ];

    const skills = [
        { name: 'Web Development', icon: 'üíª', color: '#000000' },
        { name: 'UI/UX Design', icon: 'üé®', color: '#1a1a1a' },
        { name: 'Mobile Apps', icon: 'üì±', color: '#333333' },
        { name: 'Backend APIs', icon: '‚öôÔ∏è', color: '#4d4d4d' }
    ];

    return (
        <section id="about" style={{ padding: '100px 0', background: '#ffffff' }}>
            <Container>
                <Row className="mb-5">
                    <Col md={12} className="text-center">
                        <span 
                            className="badge rounded-pill px-4 py-2 mb-3"
                            style={{
                                background: '#000000',
                                color: 'white',
                                fontSize: '0.9rem',
                                fontWeight: '600'
                            }}
                        >
                            ABOUT ME
                        </span>
                        <h2 className="display-4 fw-bold mb-3" style={{ color: '#000000' }}>
                            Who I Am
                        </h2>
                        <p className="lead text-muted" style={{ maxWidth: '700px', margin: '0 auto' }}>
                            Passionate developer with a keen eye for design and detail
                        </p>
                    </Col>
                </Row>

                <Row className="align-items-center mb-5">
                    <Col lg={6} className="mb-4 mb-lg-0">
                        <Image 
                            src="/images/about.jpg" 
                            alt="About Me" 
                            fluid
                            rounded
                            style={{
                                width: '100%',
                                boxShadow: '0 20px 60px rgba(0, 0, 0, 0.15)',
                                borderRadius: '20px'
                            }}
                        />
                    </Col>
                    <Col lg={6}>
                        <h3 className="fw-bold mb-4">Hello! I'm Naisya Yuen</h3>
                        <p className="text-muted mb-4" style={{ fontSize: '1.1rem', lineHeight: '1.8' }}>
                            I'm a passionate <strong>Full Stack Developer</strong> and <strong>UI/UX Designer</strong> based in Indonesia. 
                            With over 5 years of experience in web development, I specialize in creating beautiful, 
                            functional, and user-centered digital experiences.
                        </p>
                        <p className="text-muted mb-4" style={{ fontSize: '1.1rem', lineHeight: '1.8' }}>
                            My expertise lies in modern web technologies including React, Next.js, Node.js, and more. 
                            I love turning complex problems into simple, beautiful, and intuitive solutions.
                        </p>
                        
                        <div className="mt-4">
                            <h5 className="fw-bold mb-3">What I Do Best</h5>
                            <Row>
                                {skills.map((skill, index) => (
                                    <Col md={6} key={index} className="mb-3">
                                        <div className="d-flex align-items-start">
                                            <div 
                                                style={{
                                                    width: '50px',
                                                    height: '50px',
                                                    borderRadius: '12px',
                                                    background: skill.color,
                                                    display: 'flex',
                                                    alignItems: 'center',
                                                    justifyContent: 'center',
                                                    marginRight: '15px',
                                                    fontSize: '1.5rem'
                                                }}
                                            >
                                                {skill.icon}
                                            </div>
                                            <div>
                                                <h6 className="fw-bold mb-1">{skill.name}</h6>
                                                <p className="text-muted small mb-0">Professional expertise</p>
                                            </div>
                                        </div>
                                    </Col>
                                ))}
                            </Row>
                        </div>

                        <Button 
                            variant="dark" 
                            size="lg" 
                            className="mt-4 rounded-pill px-5"
                            onClick={() => {
                                const element = document.getElementById('contact');
                                element?.scrollIntoView({ behavior: 'smooth' });
                            }}
                        >
                            Let's Work Together
                        </Button>
                    </Col>
                </Row>

                {/* Stats */}
                <Row className="mt-5">
                    {stats.map((stat, index) => (
                        <Col md={3} key={index} className="mb-4">
                            <Card 
                                className="border-0 shadow-sm text-center h-100"
                                style={{
                                    borderRadius: '15px',
                                    transition: 'all 0.3s ease',
                                    cursor: 'pointer'
                                }}
                                onMouseEnter={(e) => {
                                    e.currentTarget.style.transform = 'translateY(-10px)';
                                    e.currentTarget.style.boxShadow = '0 15px 30px rgba(0,0,0,0.15)';
                                }}
                                onMouseLeave={(e) => {
                                    e.currentTarget.style.transform = 'translateY(0)';
                                    e.currentTarget.style.boxShadow = '0 5px 15px rgba(0,0,0,0.08)';
                                }}
                            >
                                <Card.Body className="p-4">
                                    <div style={{ fontSize: '3rem', marginBottom: '15px' }}>
                                        {stat.icon}
                                    </div>
                                    <h2 className="fw-bold mb-2" style={{ color: '#000000', fontSize: '2.5rem' }}>
                                        {stat.number}
                                    </h2>
                                    <p className="text-muted mb-0 fw-semibold">{stat.label}</p>
                                </Card.Body>
                            </Card>
                        </Col>
                    ))}
                </Row>
            </Container>
        </section>
    );
}

export default AboutSection;