"use client"
import { useState, useEffect } from 'react';
import Carousel from 'react-bootstrap/Carousel';
import { Container, Button } from 'react-bootstrap';

interface Slide {
    title: string;
    subtitle: string;
    imageUrl: string;
}

const HeroSection = () => {
    const [displayText, setDisplayText] = useState('');
    const roles = ['Full Stack Developer', 'UI/UX Designer', 'Creative Problem Solver'];
    const [roleIndex, setRoleIndex] = useState(0);
    const [charIndex, setCharIndex] = useState(0);
    const [isDeleting, setIsDeleting] = useState(false);

    useEffect(() => {
        const typingSpeed = isDeleting ? 50 : 100;
        const currentRole = roles[roleIndex];

        const timer = setTimeout(() => {
            if (!isDeleting && charIndex < currentRole.length) {
                setDisplayText(currentRole.substring(0, charIndex + 1));
                setCharIndex(charIndex + 1);
            } else if (isDeleting && charIndex > 0) {
                setDisplayText(currentRole.substring(0, charIndex - 1));
                setCharIndex(charIndex - 1);
            } else if (!isDeleting && charIndex === currentRole.length) {
                setTimeout(() => setIsDeleting(true), 1500);
            } else if (isDeleting && charIndex === 0) {
                setIsDeleting(false);
                setRoleIndex((roleIndex + 1) % roles.length);
            }
        }, typingSpeed);

        return () => clearTimeout(timer);
    }, [charIndex, isDeleting, roleIndex]);

    const slides: Slide[] = [
        {
            title: "Naisya Yuen",
            subtitle: displayText || "Full Stack Developer",
            imageUrl: "/images/banner_01.JPG",
        },
        {
            title: "Creative Solutions",
            subtitle: "Building beautiful digital experiences",
            imageUrl: "/images/banner_02.JPG",
        },
        {
            title: "Portfolio Showcase",
            subtitle: "Explore my work and projects",
            imageUrl: "/images/banner_03.jpg",
        },
    ];

    const scrollToSection = (id: string) => {
        const element = document.getElementById(id);
        element?.scrollIntoView({ behavior: 'smooth' });
    };

    return (
        <div style={{ position: 'relative' }}>
            <Carousel fade interval={6000} controls={true} indicators={true} className="hero-carousel">
                {slides.map((slide, index) => (
                    <Carousel.Item key={index}>
                        <div 
                            style={{
                                backgroundImage: `linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url(${slide.imageUrl})`,
                                backgroundSize: "cover",
                                backgroundPosition: "center",
                                height: "90vh",
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center",
                            }}
                        >
                            <Container className="text-center text-white">
                                <div style={{ maxWidth: '800px', margin: '0 auto' }}>
                                    <h1 
                                        className="display-1 fw-bold mb-4"
                                        style={{
                                            fontSize: 'clamp(3rem, 8vw, 6rem)',
                                            textShadow: '0 4px 20px rgba(0,0,0,0.5)',
                                            animation: 'fadeInUp 1s ease-out'
                                        }}
                                    >
                                        {slide.title}
                                    </h1>
                                    <p 
                                        className="lead fs-2 mb-5"
                                        style={{
                                            textShadow: '0 2px 10px rgba(0,0,0,0.5)',
                                            animation: 'fadeInUp 1s ease-out 0.3s both',
                                            minHeight: '60px',
                                            fontWeight: '300'
                                        }}
                                    >
                                        {index === 0 ? (
                                            <>
                                                {displayText}
                                                <span style={{ animation: 'blink 1s infinite' }}>|</span>
                                            </>
                                        ) : (
                                            slide.subtitle
                                        )}
                                    </p>
                                    <div style={{ animation: 'fadeInUp 1s ease-out 0.6s both' }}>
                                        <Button 
                                            size="lg"
                                            onClick={() => scrollToSection('portfolio')}
                                            className="rounded-pill px-5 py-3 me-3 fw-semibold border-0"
                                            style={{
                                                background: '#ffffff',
                                                color: '#000000',
                                                transition: 'all 0.3s ease',
                                            }}
                                            onMouseEnter={(e) => {
                                                e.currentTarget.style.transform = 'translateY(-3px)';
                                                e.currentTarget.style.boxShadow = '0 10px 30px rgba(255,255,255,0.3)';
                                            }}
                                            onMouseLeave={(e) => {
                                                e.currentTarget.style.transform = 'translateY(0)';
                                                e.currentTarget.style.boxShadow = 'none';
                                            }}
                                        >
                                            View Portfolio
                                        </Button>
                                        <Button 
                                            size="lg"
                                            onClick={() => scrollToSection('contact')}
                                            variant="outline-light"
                                            className="rounded-pill px-5 py-3 fw-semibold"
                                            style={{
                                                border: '2px solid white',
                                                transition: 'all 0.3s ease',
                                            }}
                                            onMouseEnter={(e) => {
                                                e.currentTarget.style.backgroundColor = 'white';
                                                e.currentTarget.style.color = '#000000';
                                            }}
                                            onMouseLeave={(e) => {
                                                e.currentTarget.style.backgroundColor = 'transparent';
                                                e.currentTarget.style.color = 'white';
                                            }}
                                        >
                                            Contact Me
                                        </Button>
                                    </div>
                                </div>
                            </Container>
                        </div>
                    </Carousel.Item>
                ))}
            </Carousel>
            
            <style jsx global>{`
                @keyframes fadeInUp {
                    from {
                        opacity: 0;
                        transform: translateY(30px);
                    }
                    to {
                        opacity: 1;
                        transform: translateY(0);
                    }
                }
                @keyframes blink {
                    0%, 49% { opacity: 1; }
                    50%, 100% { opacity: 0; }
                }
                .hero-carousel .carousel-indicators button {
                    width: 12px;
                    height: 12px;
                    border-radius: 50%;
                    background-color: white;
                    opacity: 0.5;
                }
                .hero-carousel .carousel-indicators .active {
                    opacity: 1;
                    transform: scale(1.2);
                }
            `}</style>
        </div>
    );
}

export default HeroSection;